import React from 'react';
import cn from 'classnames';
import { CalcTools } from '@calcTypes/index';
import classes from './makeControlElements.module.scss';
const makeControlElements = (controls, onClick) => controls.map(({ area, label }) => (React.createElement("button", { className: cn(classes[area], classes.control, {
        [classes.equalBack]: area === CalcTools.equal,
    }), key: area, onClick: () => onClick(area, label) },
    React.createElement("div", { className: classes.controlBack }),
    typeof label === 'string' ? React.createElement("img", { src: label, alt: '#' }) : label)));
export default makeControlElements;
